html {
  font-size: 16px;
}

html,
body {
  min-height: 100%;
  position: relative;
}

body {
  font-size: calc( (100vw - 328px)/(1200 - 328) * (18 - 16) + 16px);
  font-family: ProximaNova-Regular, sans-serif;
  color: #333945;
  line-height: 1.8;
}

ul,
ol,
li,
p,
h1,
