  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.btn {
  border-radius: 4px;
  text-transform: uppercase;
  padding: 16px 24px;
  border: 1px solid #CC9932;
  background: linear-gradient(360deg, #F2C453 0%, #FCE3A8 100%);
  color: #333333;
  font-family: ProximaNova-Semibold, sans-serif;
}

.title {
  font-size: calc( (100vw - 328px)/(1200 - 328) * (42 - 32) + 32px);
  font-family: ProximaNova-Semibold, sans-serif;
  line-height: 1.35;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.col {
  width: calc((100% - 38px) / 2);
}

@media only screen and (max-width: 768px) {
  .col {
    width: 100%;
  }
}

.heading {
  font-size: calc( (100vw - 328px)/(1200 - 328) * (38 - 24) + 24px);
