.info-card {
  width: calc((100% - 38px) / 2);
  box-shadow: 0px 62px 80px rgba(0, 0, 0, 0.07), 0px 22px 24px rgba(0, 0, 0, 0.04), 0px 6px 4px rgba(0, 0, 0, 0.03);
  border-radius: 4px;
  background-color: #ffffff;
  padding: 16px;
}

@media only screen and (min-width: 768px) {
  .info-card {
    padding: 33px 32px 70px;
  }
}

.info-card__title {
  padding-left: 72px;
  margin-bottom: 24px;
  color: #027FC2;
  position: relative;
}

.info-card__title:before {
  content: "";
  position: absolute;
  width: 48px;
  height: 48px;
  left: 0;
  background-repeat: no-repeat;
  background-size: contain;
}

.info-card__title--generate::before {
  background-image: url(../img/icons/generate.png);
}

.info-card__title--enhance::before {
  background-image: url(../img/icons/enhance.png);
}

.info-card__title--spot::before {
  background-image: url(../img/icons/opportunities.png);
}

.info-card__title--improve::before {
  background-image: url(../img/icons/improve.png);
}

.info-card__title--measure::before {
  background-image: url(../img/icons/measure.png);
}

.info-card__title--help::before {
  background-image: url(../img/icons/help.png);
}

.info-card__title--boost::before {
  background-image: url(../img/icons/boost.png);
}

.info-card__title--awareness::before {
  background-image: url(../img/icons/aware.png);
}

.info-card__title--dislike::before {
  background-image: url(../img/icons/dislike.png);
}

.info-card__title--trash::before {
  background-image: url(../img/icons/trash.png);
}

.info-card__title--listing::before {
  background-image: url(../img/icons/listing.png);
}

.header {
  border-bottom: 1px solid #E5E5E5;
  padding: 20px 0 30px;
}

.nav {
  display: flex;
  justify-content: space-between;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 23px;
}

.nav__link {
  text-transform: uppercase;
  padding: 16px 24px;
  border-radius: 4px;
  font-family: ProximaNova-Semibold, sans-serif;
}

.nav__link--login {
  color: #019BD3;
  border: 1px solid #019BD3;
}

.nav__link--free {
  border: 1px solid #CC9932;
  background: linear-gradient(360deg, #F2C453 0%, #FCE3A8 100%);
  color: #333333;
}

